<template>
  <div>
    <div class="buble"></div>
    <div class="line" :style="{height:h + 'px'}"></div>
    <div class="c1" ref="c1">
      <div
        style="color: #58677c;font-size: 0.875rem;font-weight: 500; float: right; text-align:right"
      >
        <a
          style="color: #58677c;font-size: 0.875rem;font-weight: 500;"
          :href="details.instituteLink"
          target="_blank"
        >{{details.institute}}</a>
        <p>
          <i>
            <a
              style="color: #58677c;font-size: 0.775rem;font-weight: 500;"
              @click.prevent="showDetails = !showDetails"
              href
            >{{showDetails?"show less...":"show more..."}}</a>
          </i>
        </p>
      </div>

      <a :href="details.link" target="_blank">
        <h6>{{details.title}}</h6>
      </a>
      <span style="font-size: 0.875rem;color: #aab4c3;font-weight:bolder">{{details.duration}}</span>
      <div v-show="showDetails">
        <p>{{details.summary}}</p>
        <h6>Achievements:</h6>
        <ul>
          <li v-for="(item, ind) in details.achievements" :key="'acheivement - ' + ind">
            <span v-html="item"></span>
            <ul v-if="details.achievementsExtra[ind + '']">
              <li
                v-for="(item1, ind1) in details.achievementsExtra[ind + ''].pubs"
                :key="'a-ext-' + ind1"
                v-html="item1"
              ></li>
            </ul>
          </li>
        </ul>
      </div>
      <h6>Key Skills:</h6>
      <b-badge
        v-for="(item, ind) in details.skills"
        :key="'skill - ' + ind"
        variant="info"
        style="margin-right:.35rem"
      >{{item}}</b-badge>
      <br />
    </div>
  </div>
</template>
<script>
export default {
  name: "Details",
  props: ["details", "ind"],
  data() {
    return {
      h: 100,
      showDetails: true
    };
  },
  mounted() {
    this.h = this.$refs.c1.clientHeight - 50;
    let c1 = this.$refs.c1;
    window.addEventListener("resize", () => {
      this.h = c1.clientHeight - 50;
    });
  },
  watch: {
    showDetails() {
      setTimeout(() => {
        this.h = this.showDetails ? this.$refs.c1.clientHeight - 50 : 0;
      }, 50);
    }
  }
};
</script>
<style scoped>
h6 {
  font-weight: bold;
  margin: 0;
}
h6 a {
  color: #80422f;
}
.buble {
  border: 4px solid #a25378;
  border-radius: 50%;
  margin: 0.2rem;
  /* background: red; */
  float: left;
  width: 0.9em;
  height: 0.9em;
}
.line {
  margin-top: 0.75em;
  margin-left: -0.77em;
  border: 2px solid #a2537866;
  border-radius: 1em;
  /* background: red; */
  float: left;
  width: 3px;
}
.c1 {
  margin-left: 30px;
  padding: 1px;
  margin-right: 10px;
}
</style>