<template>
  <div @mouseover="mouseOver = true" @mouseout="mouseOver = false">
    <h6>
      <span style="color: #58677c;font-weight: 700;">{{item.title}}</span>
      <span style="font-size: .8em">&nbsp;({{item.year}})&nbsp;</span>
      <span
        style="color: #58677c;font-size: 0.775rem;font-weight: 500; cursor: pointer"
        @click="showDetails = !showDetails"
      >{{showDetails?"🔻":"⛛"}}</span>
    </h6>
    <span v-html="item.highlight"></span>
    <ul v-show="mouseOver || showDetails">
      <li v-for="(it, ind1) in item.more" :key="ind1">{{it}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Apti",
  props: ["item"],
  data() {
    return {
      showDetails: false,
      mouseOver: false
    };
  }
};
</script>
