import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import store from './store'
import { profiles } from './store/profiles'
import { BootstrapVue } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.config.productionTip = false
Vue.use(BootstrapVue)

new Vue({
    store,
    render: h => h(App),
    data: {
        profiles,
        emails: {
            gmail: 'mayank.srmu@gmail.com',
            iitbhu: 'mayank.chaudhari.app09@iitbhu.ac.in'
        },
    },
    methods: {
        changeFillColor(target) {
            // e.target.style.transform = "scale(0.8, 0.8)";
            let child = target.children;
            for (var i = 0; i < child.length; i++)
                this.changeFillColor(child[i]);
            target.style.fill = "#9983fa";
            if (target.classList.contains('profile-link')) {
                target.style.opacity = 1;
                target.style.fontWeight = 'bolder';
            }
        },
        revertFillColor(target) {
            let child = target.children;
            for (var i = 0; i < child.length; i++)
                this.revertFillColor(child[i]);
            target.style.fill = "";
            if (target.classList.contains('profile-link')) {
                target.style.opacity = .7;
                target.style.fontWeight = 'normal';
            }
        }
    }
}).$mount('#app')