<template>
  <header>
    <div>
      <img ref="img" src="../assets/logo.png" alt="photo" />
    </div>
    <div class="hk">
      <h2 class="name">Mayank Kumar Chaudhari</h2>
      <table class="emails" @mouseover="showOverflow" @mouseout="hideOverflow">
        <tr
          v-for="(email, ind) in $root.emails"
          :key="ind"
          @mouseover="changeFillColor"
          @mouseout="revertFillColor"
        >
          <td>
            <a v-html="$root.profiles.mail[1]" :href="'mailto:'+email" target="_blank"></a>
          </td>
          <td class="email">
            <a :href="'mailto:'+email" target="_blank">{{email}}</a>
          </td>
        </tr>
        <tr @mouseover="changeFillColor" @mouseout="revertFillColor">
          <td>
            <a v-html="$root.profiles.web[1]" :href="url" target="_blank"></a>
          </td>
          <td class="email">
            <a :href="url" target="_blank">{{url}}</a>
          </td>
        </tr>
      </table>
    </div>
    <div class="profiles" @mouseover="showOverflow" @mouseout="hideOverflow">
      <table>
        <tr
          v-show="profile[0].length"
          @mouseover="changeFillColor"
          @mouseout="revertFillColor"
          v-for="(profile, ind) in $root.profiles"
          :key="ind"
        >
          <td style="text-align: center">
            <a target="_blank" :href="profile[0]" v-html="profile[1]" />
          </td>
          <td class="profile-link">
            <a :href="profile[0]" target="_blank">{{profile[0].replace(/http(s)?:\/\/(www.)?/,'')}}</a>
          </td>
        </tr>
      </table>
    </div>
  </header>
</template>
<script>
export default {
  name: "Header",
  data() {
    return {
      url: window.location
    };
  },
  methods: {
    changeFillColor(e) {
      this.$root.changeFillColor(e.currentTarget);
    },
    revertFillColor(e) {
      this.$root.revertFillColor(e.currentTarget);
    },
    showOverflow(e) {
      e.currentTarget.style.overflowY = "auto";
    },
    hideOverflow(e) {
      e.currentTarget.style.overflow = "hidden";
    }
  }
};
</script>
<style scoped>
header {
  display: flex;
  margin-top: 1cm;
  background: #80422f;
  overflow: hidden;
}
img {
  max-width: 5cm;
  height: 100%;
}
tr {
  vertical-align: center;
  padding: 2px;
}
.emails {
  text-align: start;
  padding: 10px;
}
.email {
  color: white;
  margin-left: 0.2rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 5px;
  opacity: 0.8;
}
.email > a {
  color: white;
}
.hk {
  text-align: start;
  padding-inline-start: 10px;
  padding-top: 20px;
  flex: auto;
}
.name {
  color: white;
}
.profiles {
  padding-top: 10px;
  height: 100%;
  overflow: hidden;
  text-align: start;
  max-width: 5cm;
}
.profile-link {
  color: white;
  margin-left: 0.2rem;
  white-space: nowrap;
  max-width: 4.2cm;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-top: 5px;
  padding-bottom: 5px;
  opacity: 0.8;
}
.profile-link > a {
  color: white;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>