<template>
  <div id="app" :style="{width: w + 'px'}">
    <img class="bg1" src="./assets/cv.gif" alt="Computer Vision AI in Action" />
    <img
      width="24px"
      height="24px"
      class="gear"
      src="./assets/gearstar_ani.gif"
      alt="options"
      @mouseover="gear"
      @mouseout="hideMenu"
    />
    <Header class="header" :style="{height: h + 'px', width: w + 'px'}" />
    <Main class="content" :style="{marginTop: (h + 38) + 'px', width: w + 'px'}"></Main>
    <Footer class="footer" :style="{width: w + 'px'}"></Footer>
  </div>
</template>

<script>
import Footer from "./components/Footer";
import Header from "./components/Header";
import Main from "./components/Main";

export default {
  name: "App",
  components: {
    Footer,
    Header,
    Main
  },
  data() {
    return {
      h: 1000,
      w: 1000
    };
  },
  created() {
    const maxH = 180,
      minH = 0;
    this.h = maxH;
    const maxW = 800;
    this.w = window.innerWidth - 20;
    this.w = this.w > maxW ? maxW : this.w;
  },
  mounted() {
    const maxH = 180,
      minH = 0;
    const maxW = 720;
    window.addEventListener("scroll", () => {
      this.h = maxH - window.scrollY;
      this.h = this.h < minH ? minH : this.h;
    });
    window.addEventListener("resize", () => {
      this.w = window.innerWidth - 20;
      this.w = this.w > maxW ? maxW : this.w;
    });
  },
  methods: {
    gear(e) {},
    hideMenu(e) {}
  }
};
</script>

<style>
.header {
  top: 0;
  position: fixed;
}
.gear {
  opacity: 0.3;
  position: fixed;
  right: 15px;
  top: 15px;
}
.content {
  padding: 5px;
  background: #80422f55;
}
.footer {
  padding-bottom: 40px;
  padding-left: 10px;
  padding-right: 10px;
  background: #80422f55;
  background-image: linear-gradient(to bottom, #80422f55, #a0f25fc5);
}
