<script>
import Details from "./Details";
import Apti from "./Apt";
import { edu } from "../store/edu";
import { work } from "../store/work";
import { skills } from "../store/skills";
import { apti } from "../store/aptitude";

export default {
  name: "Main",
  components: { Details, Apti },
  data() {
    return {
      edu,
      work,
      skills,
      apti,
      skillOrder: {
        def: ["fe", "data", "droid", "re"],
        fe: ["fe", "data", "droid", "re"]
      },
      flv: "def"
    };
  }
};
</script>
